body {
  background: url('./img/shark-two.png') center center fixed no-repeat;
}
.App {
  text-align: center;
}

a {
  color: gray
}

.box {
  margin: 7vw 3vw 7vw 3vw;
  display: inline-block;
  text-align: center;
  padding: 3vw;
  border: 1px solid black;
  width: 10vw;
  max-height: 20vh;
  -ms-flex-item-align: center;
      align-self: center;
}

.box-wrapper {
  width: 80vw;
  overflow: hidden;
  margin-left: 10vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.sub-box {
    color: white;
}

.shade {
  background: rgba(0,0,0,0.5);
  border-radius: 5px;
  height: 100vh;
  width: 100vw;
}

.nav-bar {
  width: 100vw;
  background-color: rgba(0,0,0,0.5);
}

.header-bar {
  width: 100vw;
  background-color: rgba(0,0,0,0.5);
  color: white;
  padding: 15px;
  margin-bottom: 25px;
}

small {
  color: gray;
  margin-left: 15px;
}

.nav-item {
  display:inline-block;
  margin: 15px;
}

.home {
  position: absolute;
  right: 15px;
  top: 5px;
}

.nav-link {
  color: white;
  text-decoration: none;
}

.preview {
  height: 100vh;
  width: 100vw;
}

.slider-animate {
  -webkit-transition: 2s;
  -o-transition: 2s;
  transition: 2s;
  -webkit-transform: translate(0, 2px);
      -ms-transform: translate(0, 2px);
          transform: translate(0, 2px);
}

.slider-reverse {
  -webkit-transition: 2s;
  -o-transition: 2s;
  transition: 2s;
  -webkit-transform: translate(0, 0);
      -ms-transform: translate(0, 0);
          transform: translate(0, 0);
}

.iframe-wrapper {
  height: 100vh;
  width: 100vw;
  margin-top: 25px;
}

.project-description {
  width: 100%;
  padding-left: 30vw;
  padding-right: 30vw;
  text-align: center;
  font-size: 25px;
  margin-top: 25px;
  color: white;
}

/* different backgrounds */
.arcade-game {
  background: url('./img/arcade.png') center center no-repeat;
}

.old-resume {
  background: url('./img/resume.jpg') center center no-repeat;
}

.basic-data {
  background: url('./img/data.png') center center no-repeat;
}

.bus-app {
    background: url('./img/data.png') center center no-repeat;
}

.bus-website {
    background: url('./img/data.png') center center no-repeat;
}

/* .languages {
    background: url('./img/ul.png') center center no-repeat;
} */

.comedy {
  background: url('./img/improv.jpg') center center no-repeat;
}

/* .frameworks {
    background: url('./img/data.png') center center no-repeat;
} */

.writing {
    background: url('./img/writing.png') center center no-repeat;
}

/* .build-tools {
    background: url('./img/data.png') center center no-repeat;
} */

.neighborhood-map {
    background: url('./img/map.png') center center no-repeat;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



